.modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba($black, .75);
    z-index: 9;

    opacity: 0;
    visibility: hidden;
    pointer-events: none;
    transition: all .25s ease-in-out;
    
    &.open {
        opacity: 1;
        visibility: visible;
        pointer-events: all;
    }

    .modal_wrap {
        padding: 2rem;
        border-radius: .5rem;
        background-color: $white;
        position: relative;
        z-index: 1;
    }

    form {
        display: grid;
        grid-template-columns: repeat(1,1fr);
        grid-gap: 1rem;
    }

    .form-footer {
        .between-word {
            display: inline-block;
            margin: 0 .5rem;
        }
        button:not(.button) {
            color: $purple;
        }
    }

    input {
        width: 100%;
    }

    .modal_close {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        cursor: pointer;
        z-index: 0;
    }
}