.user-profile_wrap {
    display: grid;
    grid-template-columns: repeat(6,6fr);
    grid-gap: 2rem;
    .section-title {
        margin-bottom: 1.5rem;
        &:not(:first-child) {
            margin-top: 2.5rem;
        }
    }
}

// столбец с данными пользователя
.user-info_data {
    grid-column: 1/3;
}

.data-fields_form {
    display: grid;
    grid-gap: 1rem;
    grid-template-columns: repeat(1,1fr);
    padding: 2rem;
    background-color: $white;
    border-radius: $mainBorderRadius;
}

.data-fields_wrap {
    input {
        width: 100%;
    }
}

.data-fields_pic {
    display: flex;
    justify-content: center;
    .data-fields_pic-wrap {
        width: 8rem;
        height: 8rem;
        border-radius: 100%;
        position: relative;
        overflow: hidden;
        img {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
        }
    }
}

// столбец с выбором подписки
.user-info_subscribe {
    grid-column: 3/6;
}

.tariff-list {
    display: grid;
    grid-template-columns: repeat(3,3fr);
    grid-gap: 1rem;
}

.tariff-item {
    border-radius: $mainBorderRadius;
    padding: 1rem;
    background-color: $white;
    transition: all .25s ease-in-out;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    &:hover {
        background-color: $purple;
        .tariff-item_title {
            color: $white;
        }
        .tariff-item_price {
            color: $white;
        }
        .button {
            border-color: $white;
            color: $white;
        }
    }
    &_title {
        transition: all .25s ease-in-out;
    }
    &_price {
        transition: all .25s ease-in-out;
        font-size: 3rem;
        margin: 1rem 0;
        color: $purple;
    }
}

.payment-history_item {
    &:not(:last-child) {
        margin-bottom: 1rem;
    }
    .payment-history_item_date,
    .payment-history_item_name {
        display: inline-block;
    }
    .payment-history_item_date {
        color: $purple;
        margin-right: 1rem;
    }
}


// столбец с выходом из ЛК
.user-info_logout {
    grid-column: 6/7;
    a {
        color: $purple;
        font-weight: bold;
        text-decoration: none;
        &:hover {
            text-decoration: underline;
        }
    }
}